
import { defineComponent, computed } from "vue";
import {
  useRouter
} from 'vue-router'

export default defineComponent({
  name: "LayoutHeader",
  data() {
    return {
      activePath: "",
      // @ts-ignore
      version: PKG_VERSION,
    };
  },
  props: {
    logo: {
      type: String,
      default: "",
    },
  },
  components: {},
  setup() {
    const router = useRouter()
    return{
      activePath: computed(() => router.currentRoute.value.path),
    }
  },
});
